#uploadProgressBarCtr {
  text-align : center;
  color      : black;
  font-weight: bolder;
  display    : block;
  line-height: 2rem;
  height     : 2rem;
}

#uploadProgressValue {
  position: absolute;
  z-index : 1;
}
.was-validated:invalid .invalid-feedback {
  display: block;
}

.ttb-container{
  background: #1d2257;
  color: #fff;
  padding: 60px 0 40px 0;
  text-align: center;
}

.red {
  padding: 0.5em;
  color  : red
}

.upload-main-content {
  padding-top:  1em;
}

.close-btn {
  position: absolute;
  right: 0;
  margin: 1em;
  z-index: 1;
}

.welcome-banner {
  background             : url(assets/jumbotron.jpg) no-repeat bottom center;
  -webkit-background-size: 100%;
  -moz-background-size   : 100%;
  -o-background-size     : 100%;
  background-size        : 100%;
  background-color       : white !important;
  padding: 0;
}


.modal-header-btn-group {
  margin-left: 1em;
}

.badge {
  margin: 3px;
}

.active-secondary-route {
  background     : transparent;
  text-decoration: underline;
}

.btn-group {
  margin-right: 1em;
}

.logo-tiny {
  transform: scale(0.2);
}

.logo-md {
  transform: scale(0.5);
}

.spl-logo {
  background   : black;
  padding      : 2em;
  border-radius: 1em;
}

.modal-full-screen {
  margin    : 0;
  width     : 100vw;
  height    : 100vh;
  max-height: 100vh !important;
  max-width : 100vw !important;
}

.modal-full-screen .modal-content {
  height: 100vh;
}

.sp-card .card-footer .btn-toolbar {
  padding: 0;
  margin : 0;
}

.btn-toolbar {
  margin: 11px 0px;
}

.secondary-side-nav a {
  padding: 10px;
  color  : #000000c9;
}

.dashboard .btn-toolbar {
  padding-bottom: 1em;
}

.react-toast-notifications__container {
  z-index: 9999 !important;
}

.dashboard {
  padding: 1em;
}

.side-nav {
  background-color: #f8f9fa !important;
  padding         : 0 !important;
}

.side-nav .nav-link {
  padding: 0 !important;
}

.app-ctr-lvl-1 {
  height: 100vh;
}

.active-side-route {
  border-right    : 5px solid #0056b3;
  background-color: #0056b312;
}

.active-top-route {
  text-decoration: underline;
  /* border-bottom   : 5px solid #0056b3;
  background-color: #0056b312; */
}

.secondary-nav a {
  color: white !important;
}


.top-nav,
.side-nav {
  padding-top   : 0 !important;
  padding-bottom: 0 !important;
}

.top-nav a,
.side-nav a {
  padding: 1em;
}

#root {
  height: 100vh;
}

.App {
  text-align: center;
}

.App-logo {
  height        : 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height      : 100vh;
  display         : flex;
  flex-direction  : column;
  align-items     : center;
  justify-content : center;
  font-size       : calc(10px + 2vmin);
  color           : white;
}

.App-link {
  color: #61dafb;
}
#historyTable
{
  margin-left: 48px;
  margin-top: 49px;
  width: 96%;
  height: auto;
  font-size: 15px;
  margin-right: 105px;

}

.react-bootstrap-table-pagination
{
  margin-right: 21px;
  margin-left: -203px;
  margin-top: 30px;
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}